
body{
    max-width: 100vw;
}

.grid{
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 1fr;
    grid-template-rows: 3em 1fr repeat(2, 2fr) 3em;
    grid-gap: 5%;
    justify-content: center;

}



  .appHeader {
      grid-column: 1/5;
      background-color: #61dafb;
  }







nav {
    background: lightgray;
    display: flex;
    height: 3rem;
    box-shadow: 1px 1px 10px gray;
    flex-wrap: nowrap;
}

nav a {
    text-decoration: none;
    color: grey;
    display: flex;
    align-items: center;
    padding: 5px 20px 5px 30px;
    flex-wrap: nowrap;
}

nav a:hover, a:active {
    background:  rgb(78, 76, 76);
    color: lightgray;
}

#logo {
    height: 3vh;
    margin: auto auto auto 10px;
}

#login{
    text-align: center;
    border-radius: 6px;
    border: 2px solid;
    margin: auto 10px auto auto ;
    padding: 5px 20px 5px 20px;
    display: flex;
}

.product{
    background-color: white;
    justify-content: center;
    align-content: center;
    flex-direction: row;
    margin: 10vh auto auto auto;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 1px 1px 20px grey;
}

.description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    min-height: 200px;
}

h2 {
    margin: 10px 10px 0 10px;
    font-size: 5vh;
}

h4 {
    margin: 0 10px 10px 10px;
    font-size: 3.5vh;
    font-weight: 100;
}

p {
    margin: 10px;
    font-size: 2.5vh;
}

.image {
    background-color: yellow;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 200px;
}

div > img {
    max-width: 60%;
    padding: 10%;
}

@media screen and (max-width: 600px) {

    .grid{
        display: flex;
        flex-direction: column;
        justify-content: center;

    }



    .appHeader {
        grid-column: 1/2;
        background-color: #61dafb;
    }




    div > img {
        max-width: 80%;
    }

    #login {
        visibility: hidden;
    }

    h2 {
        margin: 10px 10px 0 10px;
        font-size: 3vh;
    }

    h4 {
        margin: 0 10px 10px 10px;
        font-size: 2.5vh;
        font-weight: 100;
    }

    p {
        margin: 10px;
        font-size: 1.8vh;
    }
}

.pris {
    align-self: flex-end;
    font-weight: bolder;
    font-size: x-large;
    margin: 20px;
}

aside{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 4vh;
}

.card{
    height: 50vh;
    width: 30vh;
    display: flex;
    flex-direction: column;
    border: 1.5vh solid white;
    border-radius: 5%;
    justify-content: space-between;
    align-items: center;
    font-size: 8vh;
    color: red;
    box-shadow: 1px 1px 20px grey;
    margin: 100px auto  auto  auto ;
}

.bottom {
    align-self: flex-end;
    margin: 1vh;
    transform: rotate(180deg);
}

.top {
    align-self: flex-start;
    margin: 1vh;
}

span {
    color: black;
    font-size: 3vh;
}

.animate{
    transition: background-color;
}

